.App {
    text-align: center;
    width: 80%;
    margin: 10% auto 0 auto;
    text-align: left;
    color: rgb(37, 37, 37);
    max-width: 1280px;
}

.title-container {
    width: 100%;
}

.title-input {
    font-size: 50px;
    width: 100%;
    padding: 0px 5px;
}

.content-container {
    margin: 35px 0;
}

.content-input {
    outline: none;
    padding: 5px 10px;
    font-size: 18px;
}
.author-input,
.phone-input {
    font-size: 18px;
    padding: 0 10px;
    width: 100%;
    margin: 15px 0;
}
.agree-container,
.button-container {
    padding: 0 10px;
    margin: 15px 0;
}

button {
    background-color: white;
    color: rgb(30, 30, 30);
    font-size: 18px;
    font-family: var(--font-NotoSerif);
    border: 1px solid rgb(183, 183, 183);
    padding: 5px 15px;
}
hr {
    border: 0.5px solid rgb(248, 248, 248);
    margin: 10px 0;
}
button:hover {
    background-color: rgb(245, 245, 245);
    cursor: pointer;
}

button:disabled {
    background-color: rgb(245, 245, 245);
    color: rgb(183, 183, 183);
    cursor: not-allowed;
}
input {
    border: none;
    overflow: auto;
    outline: none;

    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;

    resize: none; /*remove the resize handle on the bottom right*/
    font-family: var(--font-NotoSerif);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type='number'] {
    -moz-appearance: textfield;
}

[placeholder]:empty::before {
    content: attr(placeholder);
    color: #555;
}

[placeholder]:empty:focus::before {
    content: '';
}

.error {
    font-size: 13px;
    padding: 5px 10px;
}

@media screen and (max-width: 500px) {
    .App {
        width: 95%;
    }

    .title-input {
        font-size: 30px;
    }

    .content-input,
    .author-input,
    .phone-input {
        font-size: 14px;
    }

    button {
        font-size: 15px;
        padding: 5px 12px;
    }
}
