body {
    margin: 0;
    font-family: var(--font-NotoSerif);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

:root {
    /* fonts */
    --font-NotoSerif: 'NotoSerifKR-ExtraLight';
    --font-GowunBatang: 'GowunBatang-Regular';
}

/* add font */
@font-face {
    font-family: 'NotoSerifKR-ExtraLight';
    src: url(./assets/fonts/Noto_Serif_KR/NotoSerifKR-ExtraLight.otf);
}

@font-face {
    font-family: 'GowunBatang-Regular';
    src: url(./assets/fonts/Gowun_Batang/GowunBatang-Regular.ttf);
}
